<template>
	<view class="container">
		<!-- 头部 -->
		<view class="header">
			<header-navigation @menuChange="selectChange" :tabFlag="false" :languageTxt="Language">
				<template #bg>
					<image class="back-img" src="../../static/Banner3.png"></image>
				</template>
				<template #titleName v-if="Object.keys(Language)!=''">{{Language.tab_sales.name}}</template>
			</header-navigation>
		</view>
		<!-- 头部 end -->
		<!-- 内容 -->
		<view class="content">
			<!-- 净收入 -->
			<view @click="select('netRevenue')" :class="selectText=='netRevenue'?'borderColor':''"
				class="chart-box box-padding disp-flex align-items">
				<view class="chart-arcbar">
					<qiun-data-charts type="arcbar" :opts="opts" :chartData="chartData" />
				</view>
				<view class="chart-arcbar-right flex">
					<view class="disp-flex" style="align-items: flex-end;">
						<view>
							<view class="chart-title">Net Revenue</view>
							<view class="chart-num font disp-flex">
								<view class="chart-num-sym">{{initTop.symbol}}</view>{{initTop.net_revenue}}
							</view>
						</view>
						<view
							:class="initTop.net_revenue_yoy_rate?(initTop.net_revenue_yoy_rate).indexOf('-')==-1?'colorFF4C00':'color00C693':''"
							class="font chart-num2">{{initTop.net_revenue_yoy_rate}}</view>
					</view>
					<view class="line"></view>
					<view class="disp-flex align-items">
						<view class="chart-title color9">LY</view>
						<view class="chart-num2 font color9"><text class="chart-num4">{{initTop.symbol}}</text>
							{{initTop.ly_net_revenue}}</view>
					</view>
				</view>
			</view>
			<!-- 净收入 end -->
			<!-- 列表 -->
			<view class="item-box disp-flex flex-wrap disp-justifyBetween">
				<view class="box-item" @click="select('Trans')" :class="selectText=='Trans'?'borderColor':''">
					<view class="chart-title">Trans</view>
					<view class="chart-num font">{{initTop.trans}}</view>
					<view
						:class="initTop.trans_yoy_rate?(initTop.trans_yoy_rate).indexOf('-')==-1?'colorFF4C00':'color00C693':''"
						class="font">{{initTop.trans_yoy_rate}}</view>
					<view class="chart-title font color9"><text>LY</text><text>{{initTop.ly_trans}}</text></view>
				</view>
				<view class="box-item" @click="select('AVT')" :class="selectText=='AVT'?'borderColor':''">
					<view class="chart-title">AVT</view>
					<view class="chart-num font"><text class="chart-num5">{{initTop.symbol}}</text> {{initTop.vat}}
					</view>
					<view
						:class="initTop.vat_yoy_rate?(initTop.vat_yoy_rate).indexOf('-')==-1?'colorFF4C00':'color00C693':''"
						class="font">{{initTop.vat_yoy_rate}}</view>
					<view class="chart-title font color9"><text>LY</text><text><text class="chart-num4"
								style="margin-right: 0px;">{{initTop.symbol}}</text> {{initTop.ly_vat}}</text></view>
				</view>
				<view class="box-item" @click="select('Units')" :class="selectText=='Units'?'borderColor':''">
					<view class="chart-title">Units</view>
					<view class="chart-num font">{{initTop.units}}</view>
					<view
						:class="initTop.units_yoy_rate?(initTop.units_yoy_rate).indexOf('-')==-1?'colorFF4C00':'color00C693':''"
						class="font">{{initTop.units_yoy_rate}}</view>
					<view class="chart-title font color9"><text>LY</text><text>{{initTop.ly_units}}</text></view>
				</view>
				<view class="box-item" @click="select('AUR')" :class="selectText=='AUR'?'borderColor':''">
					<view class="chart-title">AUR</view>
					<view class="chart-num font"><text class="chart-num5">{{initTop.symbol}}</text> {{initTop.aur}}
					</view>
					<view
						:class="initTop.aur_yoy_rate?(initTop.aur_yoy_rate).indexOf('-')==-1?'colorFF4C00':'color00C693':''"
						class="font">{{initTop.aur_yoy_rate}}</view>
					<view class="chart-title font color9"><text>LY</text><text><text class="chart-num4"
								style="margin-right: 0px;">{{initTop.symbol}}</text> {{initTop.ly_aur}}</text></view>
				</view>
				<view class="box-item" @click="select('UPT')" :class="selectText=='UPT'?'borderColor':''">
					<view class="chart-title">UPT</view>
					<view class="chart-num font">{{initTop.upt}}</view>
					<view
						:class="initTop.upt_yoy_rate?(initTop.upt_yoy_rate).indexOf('-')==-1?'colorFF4C00':'color00C693':''"
						class="font">{{initTop.upt_yoy_rate}}</view>
					<view class="chart-title font color9"><text>LY</text><text>{{initTop.ly_upt}}</text></view>
				</view>
				<view class="box-item" @click="select('ClientTrans')"
					:class="selectText=='ClientTrans'?'borderColor':''">
					<view class="chart-title">Traffic/CR</view>
					<view class="chart-num3 font">{{initTop.traffic}}/{{initTop.CR}}<text
							class="chart-num-sym2">%</text></view>
					<view
						:class="initTop.traffic_yoy_rate?(initTop.traffic_yoy_rate).indexOf('-')==-1?'colorFF4C00':'color00C693':''"
						class="font">{{initTop.traffic_yoy_rate}}</view>
					<view class="chart-title font color9"><text>LY</text><text>{{initTop.ly_traffic}}</text></view>
				</view>
			</view>
			<!-- 列表 end -->
			<!-- 圆柱形图表 -->
			<view class="chart-box min-chart" style="margin-top: 0px;">
				<qiun-data-charts type="mix" :opts="minOpts" :chartData="minChartData" />
			</view>
			<!-- 圆柱形图表 end -->
			<!-- 渠道分布图 -->
			<view class="chart-box channel-chart">
				<view class="chart-title" v-if="Object.keys(Language)!=''">{{Language.tab_by_area.name}}</view>
				<view class="ring-box">
					<qiun-data-charts type="ring" @getIndex="getIndex" :opts="ringOpts" :chartData="ringChartData" />
				</view>
			</view>
			<!-- 渠道分布图end -->
		</view>
		<!-- 内容 end -->
	</view>
</template>

<script>
	import uCharts from '@/uni_modules/qiun-data-charts/js_sdk/u-charts/config-ucharts.js';
	const APP = getApp();
	import headerNavigation from "@/components/header/header.vue";
	import {
		formatTime,
		getLanguageData
	} from "@/utils/utils.js";
	import {
		getSalesAnalysis,
		getSalesInfoTrend,
		getSalesByRegion
	} from '../../utils/http.js';
	export default {
		components: {
			headerNavigation
		},
		data() {
			return {
				menuIndex: 0,
				chartData: {}, //进度条数据
				trendData: {
					categories: [],
					Trans: {
						'TransLY': [],
						'Trans': []
					},
					AVT: {
						'AVTLY': [],
						'AVT': []
					},
					Units: {
						'UnitsLY': [],
						'Units': []
					},
					AUR: {
						'AURLY': [],
						'AUR': []
					},
					UPT: {
						'UPTLY': [],
						'UPT': []
					},
					ClientTrans: {
						'ClientTransLY': [],
						'ClientTrans': []
					},
					netRevenue: {
						'NetRevenueLY': [],
						'NetRevenue': []
					}
				},
				initTop: {
					"net_revenue": 0,
					"sales_rate": 0,
					"trans": 0,
					"vat": 0,
					"units": 0,
					"aur": 0,
					"upt": 0,
					"client_trans": 0,
					"ly_net_revenue": 0,
					"ly_sales_rate": 0,
					"ly_trans": 0,
					"ly_vat": 0,
					"ly_units": 0,
					"ly_aur": 0,
					"ly_upt": 0,
					"ly_client_trans": 0
				},
				opts: {
					title: {
						name: "80%",
						fontSize: 14,
					},
					extra: {
						arcbar: {
							type: "circle",
							lineCap: "butt"
						}
					}
				}, //进度条样式
				minChartData: {}, //圆柱形数据
				minOpts: {
					yAxis: {
						data: [{
								position: "right",
								title: "Traffic(人)",
								axisLine: false,
								fontColor: "#999999",
								fontSize: 10,
								titleFontSize: 10,
								titleFontColor: "999999",
								min: 0
							},
							{
								position: "left",
								title: "Net Revenue",
								textAlign: "left",
								axisLine: false,
								fontColor: "#999999",
								fontSize: 10,
								titleFontSize: 10,
								titleFontColor: "999999",
								min: 0
							},
						]
					}
				}, //圆柱形样式
				ringChartData: {}, //环形图数据
				ringOpts: {
					title: {
						name: ""
					},
					subtitle: {
						name: "By Area"
					}
				},
				timeType: "D",
				timeValue: "",
				selectText: "netRevenue",
				org0: "",
				org1: "",
				org2: "",
				org3: "",
				org4: "",
				Language: {}
			}
		},
		onLoad() {
			this.$i18n.locale = APP.globalData.code;
			getLanguageData().then((res) => {
				this.Language = res;
			})
		},
		methods: {
			getIndex(e) {
				console.log(e)
				let categories = uCharts.option[e.id].categories;
				let series = uCharts.option[e.id].series;
				console.log("获取uCharts实例", series);
				// this.ringChartData={}
			},
			// 当前选中的数据
			select(val) {
				this.selectText = val;
				let text1 = "Traffic(人)";
				let text = "";
				if (val == "netRevenue") {
					text = "Net Revenue"
				} else {
					if (val == 'Trans') {
						text = val + "(笔)";
					} else if (val == "AVT") {
						text = val + "(客单价)";
					} else if (val == "Units") {
						text = val + "(件)";
					} else if (val == "AUR") {
						text = val + "(件单价)";
					} else if (val == "UPT") {
						text = val + "(率)";
					} else if (val == 'ClientTrans') {
						text1 = "CR(率)";
						text = "Traffic(人)";
					}
				}
				this.minOpts.yAxis.data[1].title = text;
				this.minOpts.yAxis.data[0].title = text1;
				this.getMinData(val)
			},
			selectChange(data) {
				this.timeValue = data.timeValue;
				this.timeType = data.calendarData;
				this.menuIndex = data.menuIndex;
				this.org0 = data.lvl0Select;
				this.org1 = data.lvl1Select;
				this.org2 = data.lvl2Select;
				this.org3 = data.lvl3Select;
				this.org4 = data.lvl4Select;
				this.getTopInit(); //获取进度条数据
				this.getCustomSalesInfoTrend(); //获取圆柱形数据
				this.getRingData(); //获取环形图数据
			},
			getTopInit() {
				let that = this;
				that.$showLoading()
				getSalesAnalysis({
					timeType: that.timeType,
					timeValue: that.timeValue,
					org0: that.org0,
					org1: that.org1,
					org2: that.org2,
					org3: that.org3,
					org4: that.org4
				}).then(res => {
					uni.hideLoading();
					that.initTop = res[0];
					that.opts.title.name = that.initTop.sales_rate + '%';
					let list = {
						series: [{
							name: "正确率",
							color: "#3C61DF",
							data: that.initTop.sales_rate / 100
						}]
					};
					this.chartData = JSON.parse(JSON.stringify(list));
				})
			},
			// 圆柱形数据
			getCustomSalesInfoTrend() {
				let that = this;
				that.$showLoading()
				getSalesInfoTrend({
					timeType: that.timeType,
					timeValue: that.timeValue,
					org0: that.org0,
					org1: that.org1,
					org2: that.org2,
					org3: that.org3,
					org4: that.org4
				}).then(res => {
					uni.hideLoading();
					that.trendData = {
						categories: [],
						Trans: {
							'TransLY': [],
							'Trans': []
						},
						AVT: {
							'AVTLY': [],
							'AVT': []
						},
						Units: {
							'UnitsLY': [],
							'Units': []
						},
						AUR: {
							'AURLY': [],
							'AUR': []
						},
						UPT: {
							'UPTLY': [],
							'UPT': []
						},
						ClientTrans: {
							'TrafficLY': [],
							'Traffic': []
						},
						netRevenue: {
							'NetRevenueLY': [],
							'NetRevenue': []
						},
						Traffic: [],
						CR: []
					}
					for (var i = 0; i < res.length; i++) {
						that.trendData.categories[i] = res[i].timeType
						that.trendData.Trans.TransLY[i] = res[i].ly_trans
						that.trendData.Trans.Trans[i] = res[i].trans
						that.trendData.AVT.AVTLY[i] = res[i].ly_vat
						that.trendData.AVT.AVT[i] = res[i].vat
						that.trendData.Units.UnitsLY[i] = res[i].ly_units
						that.trendData.Units.Units[i] = res[i].units
						that.trendData.AUR.AURLY[i] = res[i].ly_aur
						that.trendData.AUR.AUR[i] = res[i].aur
						that.trendData.UPT.UPTLY[i] = res[i].ly_upt
						that.trendData.UPT.UPT[i] = res[i].upt
						that.trendData.ClientTrans.TrafficLY[i] = res[i].ly_Traffic
						that.trendData.ClientTrans.Traffic[i] = res[i].Traffic
						that.trendData.netRevenue.NetRevenueLY[i] = res[i].ly_net_revenue
						that.trendData.netRevenue.NetRevenue[i] = res[i].net_revenue
						that.trendData.Traffic[i] = res[i].Traffic
						that.trendData.CR[i] = res[i].CR
					}
					that.getMinData('netRevenue');
				})
			},
			getMinData(val1) {
				let that = this;
				let dataa = {};
				dataa = that.trendData[val1];
				let color = "#9EB4FF"
				let seriesData = [];
				for (let s in dataa) {
					let series = {
						name: "",
						val: []
					}
					series.name = s;
					for (var i = 0; i < dataa[s].length; i++) {
						series.val.push({
							"value": dataa[s][i],
							"color": color
						})
					}
					color = '#3C61DF'
					seriesData.push(series)
				}
				let res = {
					categories: that.trendData.categories,
					series: [{
							name: seriesData[0].name,
							index: 1,
							type: "column",
							color: "#9EB4FF",
							data: seriesData[0].val
						},
						{
							name: seriesData[1].name,
							index: 1,
							type: "column",
							color: "#3C61DF",
							data: seriesData[1].val
						},
						{
							name: seriesData[1].name == 'Traffic' ? "CR" : "Traffic",
							type: "line",
							color: "#F1A84B",
							data: seriesData[1].name == 'Traffic' ? that.trendData.CR : that.trendData.Traffic
						}

					]
				};
				this.minChartData = JSON.parse(JSON.stringify(res));
			},
			// 环形图数据
			getRingData() {
				let that = this;
				that.$showLoading()
				let color = ["#96CDCD", "#1E90FF", "#54FF9F", "#708090", "#0000CD", "#4682B4", "#F0FFFF", "#CDC9C9",
					"#CD9B1D", "#CD5C5C", "#8B658B", "#9370DB", "#EED8AE", "#FAF0E6", "#E6E6FA", "#FFC1C1"
				];
				getSalesByRegion({
					timeType: that.timeType,
					timeValue: that.timeValue,
					org0: that.org0,
					org1: that.org1,
					org2: that.org2,
					org3: that.org3,
					org4: that.org4
				}).then(res => {
					let customData = []
					for (var i = 0; i < res.length; i++) {
						let parama = {};
						parama.name = res[i].manage_region_code
						parama.value = res[i].net_revenue
						parama.color = color[i];
						customData.push(parama);
					}
					let res1 = {
						series: [{
							data: customData
						}]
					};
					this.ringChartData = JSON.parse(JSON.stringify(res1));
					that.ringOpts.title.name = that.timeValue;
				})
			}
		}
	}
</script>

